<template>
  <div class="relative">
    <!-- Dev Phases Button -->
    <button
      @click="toggleDropdown"
      class="flex items-center gap-2 px-3 py-2 text-sm font-medium text-gray-700 bg-purple-100 border border-purple-200 rounded-md hover:bg-purple-200 transition-colors"
    >
      <!-- Construction Icon -->
      <svg class="h-4 w-4" viewBox="0 0 24 24" fill="currentColor">
        <path d="M12 2L2 7L12 12L22 7L12 2Z" fill="#F59E0B"/>
        <path d="M2 17L12 22L22 17" stroke="#1F2937" stroke-width="2" fill="none"/>
        <path d="M2 12L12 17L22 12" stroke="#1F2937" stroke-width="2" fill="none"/>
      </svg>
      
      <!-- Chevron Left -->
      <svg class="h-3 w-3" fill="none" viewBox="0 0 24 24" stroke="currentColor">
        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
      </svg>
      
      <span>Dev Phases</span>
      
      <!-- Chevron Right -->
      <svg class="h-3 w-3" fill="none" viewBox="0 0 24 24" stroke="currentColor">
        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5l7 7-7 7" />
      </svg>
    </button>

    <!-- Dropdown Menu -->
    <div
      v-if="isOpen"
      class="absolute top-full left-0 mt-1 w-64 bg-white border border-gray-200 rounded-md shadow-lg z-50"
    >
      <div class="py-1">
        <div class="px-3 py-2 text-xs font-semibold text-gray-500 uppercase tracking-wider border-b border-gray-100">
          RFP Workflow Phases
        </div>
        
        <a
          v-for="phase in phases"
          :key="phase.number"
          :href="phase.path"
          class="block px-3 py-2 text-sm text-gray-700 hover:bg-gray-50 hover:text-gray-900 transition-colors"
          @click="closeDropdown"
        >
          <span class="font-medium">Phase {{ phase.number }}:</span>
          <span class="ml-1">{{ phase.name }}</span>
        </a>
        
        <div class="border-t border-gray-100 mt-1">
          <a
            href="/rfp-workflow.html"
            class="block px-3 py-2 text-sm text-gray-500 hover:bg-gray-50 hover:text-gray-700 transition-colors"
            @click="closeDropdown"
          >
            üìä Workflow Overview
          </a>
          <a
            href="/dashboard.html"
            class="block px-3 py-2 text-sm text-gray-500 hover:bg-gray-50 hover:text-gray-700 transition-colors"
            @click="closeDropdown"
          >
            üè† Dashboard
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, onUnmounted } from 'vue'

const isOpen = ref(false)

// Development phase navigation data
const phases = [
  { number: 1, name: 'Creation', path: '/rfp-creation' },
  { number: 2, name: 'Approval', path: '/rfp-approval' },
  { number: 3, name: 'Vendor Selection', path: '/rfp-vendor-selection' },
  { number: 4, name: 'URL Generation', path: '/rfp-url-generation' },
  { number: 5, name: 'Vendor Portal', path: '/rfp-vendor-portal' },
  { number: 6, name: 'Evaluation', path: '/rfp-evaluation' },
  { number: 7, name: 'Comparison', path: '/rfp-comparison' },
  { number: 8, name: 'Consensus', path: '/rfp-consensus' },
  { number: 9, name: 'Award', path: '/rfp-award' },
  { number: 10, name: 'Onboarding', path: '/rfp-onboarding' }
]

const toggleDropdown = () => {
  isOpen.value = !isOpen.value
}

const closeDropdown = () => {
  isOpen.value = false
}

// Close dropdown when clicking outside
const handleClickOutside = (event: MouseEvent) => {
  const target = event.target as Element
  if (!target.closest('.relative')) {
    isOpen.value = false
  }
}

onMounted(() => {
  document.addEventListener('click', handleClickOutside)
})

onUnmounted(() => {
  document.removeEventListener('click', handleClickOutside)
})
</script>

<style scoped>
/* Ensure dropdown appears above other elements */
.relative {
  position: relative;
}

/* Smooth transitions */
.transition-colors {
  transition: background-color 0.15s ease-in-out, color 0.15s ease-in-out;
}

/* Hover effects */
.hover\:bg-purple-200:hover {
  background-color: rgb(221 214 254);
}

.hover\:bg-gray-50:hover {
  background-color: rgb(249 250 251);
}

.hover\:text-gray-900:hover {
  color: rgb(17 24 39);
}

.hover\:text-gray-700:hover {
  color: rgb(55 65 81);
}
</style>
